import React, { useState, useEffect } from "react";
import { useParams } from "react-router";
import request from "superagent";
import loader from "../loading.gif";
import { StoryMain } from "./StoryMain";
import "./css/InnerStory.css";
import { Subscribe } from "./Subscribe";
import { Header } from "./Header";
export const InnerStory = () => {
  const [loading, setLoading] = useState(true);
  const [news, setNews] = useState({});
  const pathname = window.location.pathname;
  console.log(pathname);
  const path_arr = pathname.split("/");
  console.log(path_arr);
  path_arr.splice(0, 2);
  console.log(path_arr);
  let slug = "";
  for (var i = 0; i < path_arr.length; i++) {
    slug = slug + path_arr[i] + "/";
  }

  const fetch_data = async () => {
    let url = `https://www.thequint.com/api/v1/stories-by-slug?slug=${slug}`;
    console.log("InnerStory slug url " + url);
    try {
      const rootResponse = await request.get(url).then((res) => res.body.story);
      const newsData = rootResponse;
      console.log(newsData);
      setNews(newsData);
      setLoading(false);
    } catch (err) {
      console.log("error:" + err);
    }
  };
  useEffect(() => {
    fetch_data();
  }, []);
  if (loading) {
    return (
      <div className="">
        <div className="loading-container">
          <img
            src={loader}
            style={{ width: "50%", height: "30%", margin: "0 auto" }}
            alt=""
          ></img>
        </div>
      </div>
    );
  }
  console.log("Innerstory ");
  console.log(news);
  const headline = news.headline;
  const summary = news.summary;
  const author = news["author-name"];
  const image_url = "https://images.thequint.com/" + news["hero-image-s3-key"];
  const updated_at = news["updated-at"];
  const news_type = path_arr[3];
  const cards = news.cards;
  const data = {
    headline: headline,
    summary: summary,
    author: author,
    imageUrl: image_url,
    updatedAt: updated_at,
    news_type: news_type,
    cards: cards,
  };
  console.log(data);
  return (
    <>
      <Header></Header>
      <div className="container root-container" style={{ marginTop: "10%" }}>
        <div className="grid-col-1">
          <StoryMain news={data}></StoryMain>
          {/* <ul class="social-share-links">
        </ul> */}
        </div>
        <div className="grid-col-2">
          <div className="add-wraper">
            <span className="add-heading">ADVERTISEMENT</span>
            <div className="add-box"></div>
          </div>
          <Subscribe></Subscribe>
        </div>
      </div>
    </>
  );
};
