import React, { useEffect, useState } from "react";
import request from "superagent";
import loader from "../loading.gif";
import { SearchComponent } from "./SearchComponent";
export const SearchData = (props) => {
  const [loading, setLoading] = useState(true);
  const [searchResult, setResult] = useState([]);
  let params = window.location.search;
  params = params.split("=");
  let search = params[1];
  const fetchData = async () => {
    const url = `https://www.thequint.com/api/v1/search?q=${search}&limit=5&offset=1`;
    const searchResponse = await request.get(url).then((res) => res.body);
    // console.log(searchResponse);
    setResult(searchResponse);
    setLoading(false);
  };
  useEffect(() => {
    fetchData();
  }, []);
  if (loading) {
    return (
      <div className="">
        <div className="loading-container">
          <img
            src={loader}
            style={{ width: "50%", height: "30%", margin: "0 auto" }}
            alt=""
          ></img>
        </div>
      </div>
    );
  }
  console.log(searchResult);
  return (
    <>
      <div className="search-result">
        <ul>
          {searchResult.results.stories.map((result, index) => {
            return (
              <SearchComponent id={index} result={result}></SearchComponent>
            );
          })}
        </ul>
      </div>
    </>
  );
};
