import React from "react";
import { Link } from "react-router-dom";
export const SearchComponent = (props) => {
  const name = props.result.sections[0].name;
  const headline = props.result.headline;
  const author = props.result["author-name"];
  const update_date = new Date(props.result["created-at"]).toLocaleString();
  const image_url =
    "https://images.thequint.com/" + props.result["hero-image-s3-key"];
  const slug = props.result.slug;
  //   console.log(image_url);
  return (
    <>
      <li>
        <div className="search-card">
          <Link to={`/react-news-api/${slug}`} className="search-card-heading">
            <span>{name}</span>
          </Link>
          <div className="search-result-grid">
            <div className="search-content">
              <Link to={`/react-news-api/${slug}`} className="search-headline">
                <h2 className="headline-type-search">{headline}</h2>
              </Link>
              <div className="search-footer">
                <a href="#">{author}</a>
                <span className="divider"></span>
                <span>{update_date}</span>
              </div>
            </div>
            <Link to={`/react-news-api/${slug}`}>
              <div className="result-image-wrapper">
                <figure className="qt-image">
                  <div className="qt-image">
                    <img src={image_url} className="serch-news-image"></img>
                  </div>
                </figure>
              </div>
            </Link>
          </div>
        </div>
      </li>
    </>
  );
};
