import React, { useState, useEffect } from "react";
import request from "superagent";
import { Footer } from "./Footer";
import { Header } from "./Header";
import "./css/Search.css";
import { SearchData } from "./SearchData";
export const Search = () => {
  let params = window.location.search;
  params = params.split("=");
  let search = params[1];
  const [inputData, setInputData] = useState(search);
  const onInputChangeHandler = (e) => {
    setInputData(e.target.value);
  };
  const url =
    "http://localhost:3000/react-news-api/searchnews/search?q=" + inputData;

  return (
    <>
      <Header></Header>
      <div className="container-flex">
        <div className="search-container">
          <div className="search-container-wrapper">
            <form className="search-form">
              <div className="search-input">
                <input
                  className="seacrh__form-input"
                  value={inputData}
                  onChange={onInputChangeHandler}
                ></input>
                <a href={url} className="search-button">
                  Search
                </a>
              </div>
            </form>
          </div>
        </div>
        <SearchData search={inputData}></SearchData>
      </div>
      <Footer></Footer>
    </>
  );
};
